<div class="plugin__mobile-header">
    Convective Tracker
</div>

<section class="plugin__content">
    <!-- LOGO ADICIONADO NO TOPO -->
    <div class="plugin-logo-container">
        <div class="logo-title">Convective Tracker</div>
    </div>
    
    <!-- Ttulo original mantido mas pode ser escondido se preferir -->
    <div class="plugin__title plugin__title--chevron-back" style="display: none;">
        Convective Tracker
    </div>

    <!-- Controles do Plugin -->
    <div class="tracker-controls">
        <!-- Seo para definir horrio manual SIMPLIFICADA -->
        <div class="control-section">
            <h3>Controle de Tempo</h3>
            <div class="time-control-section">
                <div class="manual-time-input">
                    <div class="time-input-row">
                        <div class="input-group">
                            <label for="manualHours">Hora:</label>
                            <input 
                                type="number" 
                                id="manualHours" 
                                min="0" 
                                max="23" 
                                bind:value={manualHours}
                                placeholder="HH"
                            />
                        </div>
                        <div class="input-group">
                            <label for="manualMinutes">Minutos:</label>
                            <input 
                                type="number" 
                                id="manualMinutes" 
                                min="0" 
                                max="59" 
                                bind:value={manualMinutes}
                                placeholder="MM"
                            />
                        </div>
                        <button class="btn btn-secondary" on:click={setCurrentTime}>
                            Agora
                        </button>
                    </div>
                    <div class="time-display-info">
                        <small>Prximo ponto: {getNextPointTimeDisplay()}</small>
                    </div>
                </div>
            </div>
        </div>

        <div class="control-section">
            <h3>Marcador de Pontos</h3>
            <div class="button-group">
                <button class="btn btn-primary" on:click={toggleTrackingMode}>
                    {trackingMode ? 'Parar Marcao' : 'Iniciar Marcao'}
                </button>
                <button class="btn btn-warning" on:click={resetTracking} disabled={points.length === 0}>
                    Reiniciar Tracking
                </button>
                <button class="btn btn-secondary" on:click={clearAll} disabled={points.length === 0}>
                    Limpar Tudo
                </button>
            </div>
            
            <div class="info-display">
                <p>Pontos marcados: {points.length}</p>
                <p>Modo: <span class="status-indicator {trackingMode ? 'active' : 'inactive'}">
                    {trackingMode ? 'ATIVO' : 'INATIVO'}
                </span></p>
                
                {#if lastCalculation}
                    <div class="calculation-result">
                        <h4>Resultados dos Clculos:</h4>
                        <p><strong>Velocidade atual:</strong> {lastCalculation.currentVelocity.toFixed(1)} m/s 
                        <span class="unit-note">({(lastCalculation.currentVelocity * 3.6).toFixed(1)} km/h)</span></p>
                        <p><strong>Direo atual:</strong> {lastCalculation.currentDirection.toFixed(0)}</p>
                        
                        {#if lastCalculation.hasSignificantAcceleration}
                            <p><strong>Acelerao:</strong> {lastCalculation.acceleration.toFixed(4)} m/s
                            <span class="unit-note">({(lastCalculation.acceleration * 12960).toFixed(1)} km/h por hora)</span></p>
                            <p><strong>Status:</strong> <span class="significant">SIGNIFICANTE</span></p>
                        {:else}
                            <p><strong>Acelerao:</strong> {lastCalculation.acceleration.toFixed(4)} m/s</p>
                            <p><strong>Status:</strong> <span class="insignificant">insignificante</span></p>
                        {/if}
                        
                        <p><strong>Distncia total:</strong> {lastCalculation.totalDistance.toFixed(1)} km</p>
                        <p><strong>Tempo total:</strong> {(lastCalculation.totalTime/60).toFixed(1)} min</p>
                        
                        <p class="calculation-method">
                            Mtodo: {lastCalculation.useUniformMotion ? 'Movimento Uniforme' : 'Movimento Uniformemente Variado'}
                        </p>
                    </div>
                {/if}
            </div>
        </div>

        <div class="control-section">
            <h3>Projeo de Trajetria</h3>
            
            <div class="projection-controls">
                <div class="time-input">
                    <label for="projectionTime">Tempo de projeo (minutos):</label>
                    <div class="time-slider-container">
                        <input 
                            type="range" 
                            id="projectionTime" 
                            min="1" 
                            max="120" 
                            step="1" 
                            bind:value={projectionTime}
                        />
                        <div class="slider-ticks">
                            <span>1</span><span>30</span><span>60</span><span>90</span><span>120</span>
                        </div>
                    </div>
                    <div class="time-display">
                        <span>{projectionTime} minutos</span>
                        <span class="unit-note">({(projectionTime/60).toFixed(1)} horas)</span>
                    </div>
                </div>
                
                <div class="projection-options">
                    <label class="checkbox-label">
                        <input type="checkbox" bind:checked={useUniformMotion} />
                        <span>Usar movimento uniforme (ignorar acelerao)</span>
                    </label>
                    
                    <label class="checkbox-label">
                        <input type="checkbox" bind:checked={keepProjectionLines} />
                        <span>Manter linhas de projeo</span>
                    </label>
                </div>
                
                <div class="button-group">
                    <button class="btn btn-primary" on:click={projectTrajectory} disabled={!lastCalculation || points.length < 2}>
                        Projetar Trajetria
                    </button>
                    
                    <button class="btn btn-secondary" on:click={clearProjection}>
                        Limpar Projeo
                    </button>
                </div>
            </div>
            
            {#if projectedPosition}
                <div class="projection-result {projectedPosition.useAcceleration ? 'accelerated' : 'uniform'}">
                    <h4>Posio Projetada:</h4>
                    <p><strong>Latitude:</strong> {projectedPosition.lat.toFixed(4)}</p>
                    <p><strong>Longitude:</strong> {projectedPosition.lon.toFixed(4)}</p>
                    <p><strong>Distncia:</strong> {projectedPosition.distance.toFixed(1)} km</p>
                    <p><strong>Intervalo:</strong> {projectionTime} minutos</p>
                    
                    {#if projectedPosition.estimatedArrival}
                        <p><strong>Chegada estimada:</strong> {projectedPosition.estimatedArrival}</p>
                    {/if}
                    
                    {#if projectedPosition.finalVelocity}
                        <p><strong>Velocidade final estimada:</strong> {projectedPosition.finalVelocity.toFixed(1)} m/s 
                        <span class="unit-note">({(projectedPosition.finalVelocity * 3.6).toFixed(1)} km/h)</span></p>
                    {/if}
                    
                    <p><strong>Mtodo:</strong> {projectedPosition.useAcceleration ? 
                        'Movimento Uniformemente Variado (com acelerao)' : 
                        'Movimento Uniforme (sem acelerao)'}</p>
                </div>
            {/if}
        </div>

        <div class="control-section">
            <h3>Histrico de Pontos</h3>
            <div class="points-list">
                {#each points as point, i}
                    <div class="point-item">
                        <div class="point-header">
                            <span class="point-number">Ponto #{i + 1}</span>
                            <span class="point-time">{formatTime(point.time)}</span>
                        </div>
                        <div class="point-coords">
                            <span>Lat: {point.lat.toFixed(4)}</span>
                            <span>Lon: {point.lon.toFixed(4)}</span>
                        </div>
                        <div class="point-time-info">
                            <small>{formatDateTime(point.time)}</small>
                        </div>
                        {#if i > 0}
                            <div class="point-metrics">
                                <span>Distncia: {point.distanceFromPrev ? point.distanceFromPrev.toFixed(1) : 'N/A'} km</span>
                                <span>Intervalo: {point.timeFromPrev ? (point.timeFromPrev/1000/60).toFixed(1) : 'N/A'} min</span>
                            </div>
                        {/if}
                    </div>
                {:else}
                    <div class="no-points">
                        Nenhum ponto marcado ainda. Ative o modo de marcao e clique no mapa.
                    </div>
                {/each}
            </div>
        </div>
    </div>
    
    <!-- RODAP -->
    <div class="plugin-footer">
        Desenvolvido por Andrew Lemos
    </div>
</section>

<script lang="ts">
    import { onDestroy, onMount } from 'svelte';
    
    // IMPORTAES DO WINDY
    import { singleclick } from '@windy/singleclick';
    import { map, markers } from '@windy/map';
    import config from './pluginConfig';

    const { name } = config;

    interface TrackingPoint {
        lat: number;
        lon: number;
        time: number; // timestamp em milissegundos
        timestamp: string; // timestamp formatado
        distanceFromPrev?: number;
        timeFromPrev?: number;
        velocity?: number; // velocidade deste segmento
        direction?: number; // direo deste segmento
    }

    interface CalculationResult {
        currentVelocity: number; // velocidade mais recente
        avgVelocity: number; // velocidade mdia
        acceleration: number;
        currentDirection: number; // direo mais recente
        avgDirection: number; // direo mdia
        hasSignificantAcceleration: boolean;
        useUniformMotion: boolean;
        points: TrackingPoint[];
        totalDistance: number;
        totalTime: number;
        lastSegmentTime?: number; // tempo do ltimo segmento em segundos
        directionAngle?: number; // inclinao da direo em graus
    }

    interface ProjectedPosition {
        lat: number;
        lon: number;
        distance: number;
        estimatedArrival?: string;
        useAcceleration?: boolean;
        finalVelocity?: number;
    }

    const EARTH_RADIUS_KM = 6371;
    const EARTH_RADIUS_M = 6371000;
    const SIGNIFICANT_ACCELERATION = 0.001;

    // Variveis principais
    let points: TrackingPoint[] = [];
    let trackingMode = false;
    let leafletMarkers: L.Marker[] = [];
    let leafletLines: L.Polyline[] = [];
    let projectionLine: L.Polyline | null = null;
    let lastCalculation: CalculationResult | null = null;
    let projectionTime = 60;
    let projectedPosition: ProjectedPosition | null = null;
    let useUniformMotion = false;
    let keepProjectionLines = true;

    // Variveis para horrio manual SIMPLIFICADO
    let manualHours = 14;
    let manualMinutes = 0;

    // Funo para definir hora atual
    function setCurrentTime() {
        const now = new Date();
        manualHours = now.getHours();
        manualMinutes = now.getMinutes();
    }

    // Obter timestamp - SIMPLIFICADO
    function getManualTimestamp(): number {
        try {
            let hours = manualHours;
            let minutes = manualMinutes;
            
            if (isNaN(hours) || hours < 0 || hours > 23) hours = 14;
            if (isNaN(minutes) || minutes < 0 || minutes > 59) minutes = 0;
            
            const now = new Date();
            const date = new Date(
                now.getFullYear(),
                now.getMonth(),
                now.getDate(),
                hours,
                minutes,
                0,
                0
            );
            
            return date.getTime();
        } catch (error) {
            console.error(' Erro ao criar timestamp:', error);
            return Date.now();
        }
    }

    // Mostrar prximo horrio que ser usado
    function getNextPointTimeDisplay(): string {
        try {
            const timestamp = getManualTimestamp();
            return formatDateTime(timestamp);
        } catch (error) {
            return 'Horrio invlido';
        }
    }

    // Formatar hora
    function formatTime(timestamp: number): string {
        try {
            const date = new Date(timestamp);
            if (isNaN(date.getTime())) return '--:--:--';
            
            return date.toLocaleTimeString([], {hour: '2-digit', minute: '2-digit', second: '2-digit'});
        } catch (error) {
            return '--:--:--';
        }
    }

    // Formatar data e hora
    function formatDateTime(timestamp: number): string {
        try {
            const date = new Date(timestamp);
            if (isNaN(date.getTime())) return 'Data/hora invlida';
            
            return date.toLocaleString([], {
                year: 'numeric',
                month: '2-digit',
                day: '2-digit',
                hour: '2-digit',
                minute: '2-digit'
            });
        } catch (error) {
            return 'Data/hora invlida';
        }
    }

    // FUNES DE CLCULO
    function calculateDistanceMeters(point1: TrackingPoint, point2: TrackingPoint): number {
        const 1 = point1.lat * Math.PI / 180;
        const 2 = point2.lat * Math.PI / 180;
        const  = (point2.lat - point1.lat) * Math.PI / 180;
        const  = (point2.lon - point1.lon) * Math.PI / 180;
        
        const a = Math.sin(/2) * Math.sin(/2) +
                Math.cos(1) * Math.cos(2) *
                Math.sin(/2) * Math.sin(/2);
        const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
        
        return EARTH_RADIUS_M * c;
    }

    function calculateDistanceKm(point1: TrackingPoint, point2: TrackingPoint): number {
        return calculateDistanceMeters(point1, point2) / 1000;
    }

    function calculateVelocity(point1: TrackingPoint, point2: TrackingPoint): number {
        const distanceM = calculateDistanceMeters(point1, point2);
        const timeDiffS = (point2.time - point1.time) / 1000;
        
        if (timeDiffS <= 0) return 0;
        return distanceM / timeDiffS;
    }

    function calculateAcceleration(points: TrackingPoint[]): {acceleration: number, hasSignificant: boolean} {
        if (points.length < 3) return {acceleration: 0, hasSignificant: false};
        
        const lastPoints = points.slice(-3);
        
        const v1 = calculateVelocity(lastPoints[0], lastPoints[1]);
        const v2 = calculateVelocity(lastPoints[1], lastPoints[2]);
        
        const t1 = (lastPoints[1].time - lastPoints[0].time) / 1000;
        const t2 = (lastPoints[2].time - lastPoints[1].time) / 1000;
        
        if (t1 <= 0 || t2 <= 0) return {acceleration: 0, hasSignificant: false};
        
        const t = t2;
        
        const acceleration = (v2 - v1) / t;
        
        const velocityChangePercent = Math.abs((v2 - v1) / v1) * 100;
        const isSignificantByPercent = velocityChangePercent > 10;
        
        const isSignificantByAbsolute = Math.abs(acceleration) > SIGNIFICANT_ACCELERATION;
        
        const hasSignificant = isSignificantByAbsolute || isSignificantByPercent;
        
        console.log(` ACELERAO DETALHADA:`);
        console.log(`   v1 = ${v1.toFixed(2)} m/s (${(v1 * 3.6).toFixed(1)} km/h)`);
        console.log(`   v2 = ${v2.toFixed(2)} m/s (${(v2 * 3.6).toFixed(1)} km/h)`);
        console.log(`   v = ${(v2 - v1).toFixed(2)} m/s`);
        console.log(`   t = ${t.toFixed(0)} s`);
        console.log(`   a = (v2 - v1) / t = ${(v2 - v1).toFixed(2)} / ${t.toFixed(0)}`);
        console.log(`   a = ${acceleration.toFixed(4)} m/s`);
        console.log(`   Mudana percentual: ${velocityChangePercent.toFixed(1)}%`);
        console.log(`   Limite absoluto: ${SIGNIFICANT_ACCELERATION} m/s`);
        console.log(`   Significativo por absoluto? ${isSignificantByAbsolute}`);
        console.log(`   Significativo por percentual? ${isSignificantByPercent}`);
        console.log(`   ACELERAO SIGNIFICATIVA? ${hasSignificant ? 'SIM ' : 'NO '}`);
        
        return {acceleration, hasSignificant};
    }

    function calculateDirection(point1: TrackingPoint, point2: TrackingPoint): number {
        const 1 = point1.lat * Math.PI / 180;
        const 2 = point2.lat * Math.PI / 180;
        const 1 = point1.lon * Math.PI / 180;
        const 2 = point2.lon * Math.PI / 180;
        
        const y = Math.sin(2 - 1) * Math.cos(2);
        const x = Math.cos(1) * Math.sin(2) - Math.sin(1) * Math.cos(2) * Math.cos(2 - 1);
        const  = Math.atan2(y, x);
        
        return ( * 180 / Math.PI + 360) % 360;
    }

    function calculateAverageDirection(directions: number[]): number {
        if (directions.length === 0) return 0;
        
        let x = 0;
        let y = 0;
        
        directions.forEach(dir => {
            const rad = dir * Math.PI / 180;
            x += Math.cos(rad);
            y += Math.sin(rad);
        });
        
        const avgRad = Math.atan2(y, x);
        return (avgRad * 180 / Math.PI + 360) % 360;
    }

    function estimateNextDirection(currentDirection: number, previousDirection: number): number {
        const diff = currentDirection - previousDirection;
        
        let normalizedDiff = diff;
        while (normalizedDiff > 180) normalizedDiff -= 360;
        while (normalizedDiff < -180) normalizedDiff += 360;
        
        const extrapolationFactor = 1.5;
        let nextDirection = currentDirection + (normalizedDiff * extrapolationFactor);
        
        nextDirection = (nextDirection + 360) % 360;
        
        console.log(` ESTIMATIVA DE DIREO:`);
        console.log(`   Anterior: ${previousDirection.toFixed(0)}`);
        console.log(`   Atual: ${currentDirection.toFixed(0)}`);
        console.log(`   Diferena: ${normalizedDiff.toFixed(0)}`);
        console.log(`   Fator de extrapolao: ${extrapolationFactor}`);
        console.log(`   Prximo estimado: ${nextDirection.toFixed(0)}`);
        
        return nextDirection;
    }

    function toggleTrackingMode() {
        trackingMode = !trackingMode;
        console.log('Modo tracking:', trackingMode ? 'ATIVO' : 'INATIVO');
        
        if (trackingMode) {
            console.log(' Pronto para marcar pontos! Clique no mapa.');
        }
    }

    function resetTracking() {
        trackingMode = false;
        
        if (points.length >= 2) {
            calculateTrajectory();
        }
        
        if (projectedPosition) {
            clearProjection();
        }
    }

    function addWindyMarker(lat: number, lon: number, pointNumber: number, timestamp: string) {
        try {
            const marker = L.marker([lat, lon], {
                icon: markers.pulsatingIcon,
                title: `Ponto ${pointNumber} - ${timestamp}`
            }).addTo(map);
            
            marker.bindTooltip(`Ponto ${pointNumber}<br>${timestamp}`, {
                permanent: false,
                direction: 'top'
            });
            
            marker.bindPopup(`
                <div style="text-align: center; padding: 5px;">
                    <strong>Ponto ${pointNumber}</strong><br>
                    ${timestamp}<br>
                    Lat: ${lat.toFixed(4)}<br>
                    Lon: ${lon.toFixed(4)}
                </div>
            `);
            
            leafletMarkers.push(marker);
            console.log(` Marcador ${pointNumber} adicionado`);
            
            return marker;
        } catch (error) {
            console.error(' Erro ao adicionar marcador:', error);
            return null;
        }
    }

    function handleMapClick(lat: number, lon: number) {
        if (!trackingMode) {
            console.log(' Modo tracking inativo. Ative primeiro clicando em "Iniciar Marcao"');
            return;
        }
        
        try {
            const currentTimestamp = getManualTimestamp();
            const timestampDisplay = formatTime(currentTimestamp);
            
            const newPoint: TrackingPoint = {
                lat,
                lon,
                time: currentTimestamp,
                timestamp: timestampDisplay
            };
            
            if (points.length > 0) {
                const lastPoint = points[points.length - 1];
                newPoint.distanceFromPrev = calculateDistanceKm(lastPoint, newPoint);
                newPoint.timeFromPrev = currentTimestamp - lastPoint.time;
                
                if (newPoint.timeFromPrev > 0) {
                    newPoint.velocity = calculateVelocity(lastPoint, newPoint);
                    newPoint.direction = calculateDirection(lastPoint, newPoint);
                    console.log(`  Tempo: ${(newPoint.timeFromPrev/1000/60).toFixed(1)} min`);
                    console.log(` Velocidade: ${newPoint.velocity?.toFixed(1)} m/s (${(newPoint.velocity! * 3.6).toFixed(1)} km/h)`);
                    console.log(` Direo: ${newPoint.direction?.toFixed(1)}`);
                }
            }
            
            points = [...points, newPoint];
            console.log(` Ponto ${points.length} marcado:`, { 
                lat: lat.toFixed(4), 
                lon: lon.toFixed(4), 
                time: formatDateTime(currentTimestamp)
            });
            
            addWindyMarker(lat, lon, points.length, timestampDisplay);
            
            if (points.length >= 2) {
                drawTrajectoryLine();
                calculateTrajectory();
            }
            
        } catch (error) {
            console.error(' Erro ao marcar ponto:', error);
            alert('Erro ao marcar ponto. Verifique os dados.');
        }
    }

    function drawTrajectoryLine() {
        if (points.length < 2) return;
        
        leafletLines.forEach(line => {
            if (map && line) map.removeLayer(line);
        });
        leafletLines = [];
        
        const latLngs = points.map(p => [p.lat, p.lon] as [number, number]);
        const line = L.polyline(latLngs, {
            color: '#ff0000',
            weight: 3,
            opacity: 0.7,
            dashArray: null
        }).addTo(map);
        
        leafletLines.push(line);
    }

    function calculateTrajectory() {
        if (points.length < 2) {
            console.log('Precisa de pelo menos 2 pontos para calcular trajetria');
            return;
        }
        
        const velocities: number[] = [];
        const directions: number[] = [];
        let totalDistanceM = 0;
        let totalTimeS = 0;
        let lastSegmentTime = 0;
        
        for (let i = 0; i < points.length - 1; i++) {
            const velocity = calculateVelocity(points[i], points[i + 1]);
            velocities.push(velocity);
            directions.push(calculateDirection(points[i], points[i + 1]));
            
            totalDistanceM += calculateDistanceMeters(points[i], points[i + 1]);
            const segmentTime = (points[i + 1].time - points[i].time) / 1000;
            totalTimeS += segmentTime;
            
            if (i === points.length - 2) {
                lastSegmentTime = segmentTime;
            }
        }
        
        if (totalTimeS <= 0) {
            console.error(' Tempo total invlido para clculo.');
            lastCalculation = null;
            return;
        }
        
        const avgVelocity = velocities.reduce((a, b) => a + b, 0) / velocities.length;
        const currentVelocity = velocities.length > 0 ? velocities[velocities.length - 1] : avgVelocity;
        
        const {acceleration, hasSignificant} = calculateAcceleration(points);
        
        const avgDirection = calculateAverageDirection(directions);
        const currentDirection = directions.length > 0 ? directions[directions.length - 1] : avgDirection;
        
        let directionAngle = currentDirection;
        if (directions.length >= 2) {
            directionAngle = estimateNextDirection(
                directions[directions.length - 1],
                directions[directions.length - 2]
            );
        }
        
        let shouldUseUniformMotion = false;
        
        if (useUniformMotion) {
            shouldUseUniformMotion = true;
            console.log(' Usando MRU (usurio escolheu movimento uniforme)');
        } else if (hasSignificant) {
            shouldUseUniformMotion = false;
            console.log(' Usando MRUV (acelerao significativa detectada)');
        } else {
            shouldUseUniformMotion = true;
            console.log(' Usando MRU (acelerao insignificante)');
        }
        
        let estimatedInstantVelocity = currentVelocity;
        if (hasSignificant && !shouldUseUniformMotion && lastSegmentTime > 0) {
            estimatedInstantVelocity = currentVelocity + acceleration * lastSegmentTime;
            console.log(` VELOCIDADE INSTANTNEA COM ACELERAO:`);
            console.log(`   v0 = ${currentVelocity.toFixed(2)} m/s`);
            console.log(`   a = ${acceleration.toFixed(4)} m/s`);
            console.log(`   t = ${lastSegmentTime.toFixed(0)} s`);
            console.log(`   v = ${currentVelocity.toFixed(2)} + ${acceleration.toFixed(4)} * ${lastSegmentTime.toFixed(0)}`);
            console.log(`   v = ${estimatedInstantVelocity.toFixed(2)} m/s`);
        }
        
        lastCalculation = {
            currentVelocity: estimatedInstantVelocity,
            avgVelocity,
            acceleration,
            currentDirection: directionAngle,
            avgDirection,
            hasSignificantAcceleration: hasSignificant,
            useUniformMotion: shouldUseUniformMotion,
            points: [...points],
            totalDistance: totalDistanceM / 1000,
            totalTime: totalTimeS,
            lastSegmentTime,
            directionAngle
        };
        
        console.log('='.repeat(60));
        console.log(' RESUMO DA TRAJETRIA CALCULADA:');
        console.log('='.repeat(60));
        console.log(`   Velocidade atual: ${estimatedInstantVelocity.toFixed(1)} m/s (${(estimatedInstantVelocity * 3.6).toFixed(1)} km/h)`);
        console.log(`   Velocidade mdia: ${avgVelocity.toFixed(1)} m/s`);
        console.log(`   Direo atual: ${currentDirection.toFixed(0)}`);
        console.log(`   Direo estimada: ${directionAngle.toFixed(0)}`);
        console.log(`   Acelerao: ${acceleration.toFixed(4)} m/s`);
        console.log(`   Status acelerao: ${hasSignificant ? 'SIGNIFICANTE ' : 'insignificante'}`);
        console.log(`   Mtodo selecionado: ${shouldUseUniformMotion ? 'MRU (Movimento Uniforme)' : 'MRUV (Movimento Uniformemente Variado)'}`);
        console.log(`   Distncia total: ${(totalDistanceM/1000).toFixed(1)} km`);
        console.log(`   Tempo total: ${(totalTimeS/60).toFixed(1)} min`);
        console.log('='.repeat(60));
    }

    function calculateDestinationPoint(lat: number, lon: number, bearing: number, distanceKm: number): {lat: number, lon: number} {
        const R = EARTH_RADIUS_KM;
        const distanceRad = distanceKm / R;
        const bearingRad = bearing * Math.PI / 180;
        
        const lat1 = lat * Math.PI / 180;
        const lon1 = lon * Math.PI / 180;
        
        const lat2 = Math.asin(
            Math.sin(lat1) * Math.cos(distanceRad) + 
            Math.cos(lat1) * Math.sin(distanceRad) * Math.cos(bearingRad)
        );
        
        const lon2 = lon1 + Math.atan2(
            Math.sin(bearingRad) * Math.sin(distanceRad) * Math.cos(lat1),
            Math.cos(distanceRad) - Math.sin(lat1) * Math.sin(lat2)
        );
        
        return {
            lat: lat2 * 180 / Math.PI,
            lon: ((lon2 * 180 / Math.PI + 540) % 360) - 180
        };
    }

    function projectTrajectory() {
        if (!lastCalculation || points.length < 2) {
            console.log('No h clculo disponvel para projetar');
            return;
        }
        
        try {
            const lastPoint = points[points.length - 1];
            const timeInSeconds = projectionTime * 60;
            
            let projectedDistanceM: number;
            let finalVelocityMps: number;
            let useAcceleration = false;
            
            console.log('='.repeat(60));
            console.log(' INICIANDO PROJEO DE TRAJETRIA');
            console.log('='.repeat(60));
            console.log(`   Tempo de projeo: ${timeInSeconds} s (${projectionTime} min)`);
            console.log(`   Velocidade atual: ${lastCalculation.currentVelocity.toFixed(2)} m/s (${(lastCalculation.currentVelocity * 3.6).toFixed(1)} km/h)`);
            console.log(`   Acelerao: ${lastCalculation.acceleration.toFixed(4)} m/s`);
            console.log(`   Direo: ${lastCalculation.currentDirection.toFixed(0)}`);
            console.log(`   Acelerao significativa? ${lastCalculation.hasSignificantAcceleration ? 'SIM ' : 'NO '}`);
            console.log(`   Config. usurio (usar MRU): ${useUniformMotion ? 'SIM' : 'NO'}`);
            console.log(`   Mtodo calculado: ${lastCalculation.useUniformMotion ? 'MRU' : 'MRUV'}`);
            
            let forceUseAcceleration = false;
            if (lastCalculation.hasSignificantAcceleration && !useUniformMotion) {
                forceUseAcceleration = true;
                console.log(' FORANDO USO DE MRUV (acelerao significativa detectada)');
            }
            
            if (lastCalculation.useUniformMotion && !forceUseAcceleration) {
                projectedDistanceM = lastCalculation.currentVelocity * timeInSeconds;
                finalVelocityMps = lastCalculation.currentVelocity;
                useAcceleration = false;
                
                console.log('\n PROJEO MRU (Movimento Uniforme):');
                console.log(`   Frmula: s = v * t`);
                console.log(`   v = ${lastCalculation.currentVelocity.toFixed(2)} m/s`);
                console.log(`   t = ${timeInSeconds} s`);
                console.log(`   s = ${lastCalculation.currentVelocity.toFixed(2)} * ${timeInSeconds}`);
                console.log(`   s = ${projectedDistanceM.toFixed(0)} m`);
            } else {
                const v0 = lastCalculation.currentVelocity;
                const a = lastCalculation.acceleration;
                
                const part1 = v0 * timeInSeconds;
                const part2 = 0.5 * a * timeInSeconds * timeInSeconds;
                projectedDistanceM = part1 + part2;
                
                finalVelocityMps = v0 + a * timeInSeconds;
                
                useAcceleration = true;
                
                console.log('\n PROJEO MRUV (Movimento Uniformemente Variado):');
                console.log(`   Frmula: s = v0*t + 0.5*a*t`);
                console.log(`   v0 = ${v0.toFixed(2)} m/s`);
                console.log(`   a = ${a.toFixed(4)} m/s`);
                console.log(`   t = ${timeInSeconds} s`);
                console.log(`   v0*t = ${v0.toFixed(2)} * ${timeInSeconds} = ${part1.toFixed(0)} m`);
                console.log(`   0.5*a*t = 0.5 * ${a.toFixed(4)} * ${timeInSeconds} = ${part2.toFixed(0)} m`);
                console.log(`   s = ${part1.toFixed(0)} + ${part2.toFixed(0)} = ${projectedDistanceM.toFixed(0)} m`);
                console.log(`\n Velocidade final (v = v0 + a*t):`);
                console.log(`   v = ${v0.toFixed(2)} + ${a.toFixed(4)} * ${timeInSeconds}`);
                console.log(`   v = ${v0.toFixed(2)} + ${(a * timeInSeconds).toFixed(2)}`);
                console.log(`   v = ${finalVelocityMps.toFixed(2)} m/s`);
            }
            
            const MAX_DISTANCE_M = 10000 * 1000;
            const MAX_VELOCITY_MPS = 500;
            
            if (projectedDistanceM < 0) {
                console.warn(` Distncia negativa detectada: ${projectedDistanceM.toFixed(0)} m, ajustando para 0`);
                projectedDistanceM = 0;
                finalVelocityMps = 0;
            }
            
            if (projectedDistanceM > MAX_DISTANCE_M) {
                console.warn(` Distncia muito alta: ${projectedDistanceM.toFixed(0)} m, limitando para ${MAX_DISTANCE_M} m`);
                projectedDistanceM = MAX_DISTANCE_M;
            }
            
            if (finalVelocityMps < 0) finalVelocityMps = 0;
            if (finalVelocityMps > MAX_VELOCITY_MPS) {
                console.warn(` Velocidade final muito alta: ${finalVelocityMps.toFixed(1)} m/s, limitando para ${MAX_VELOCITY_MPS} m/s`);
                finalVelocityMps = MAX_VELOCITY_MPS;
            }
            
            const projectedDistanceKm = projectedDistanceM / 1000;
            
            console.log(`\n DISTNCIA FINAL PROJETADA: ${projectedDistanceKm.toFixed(1)} km`);
            
            const bearing = lastCalculation.currentDirection;
            
            const destination = calculateDestinationPoint(
                lastPoint.lat,
                lastPoint.lon,
                bearing,
                projectedDistanceKm
            );
            
            if (destination.lat < -90 || destination.lat > 90 || 
                destination.lon < -180 || destination.lon > 180) {
                console.warn(` Coordenadas fora dos limites: Lat=${destination.lat.toFixed(4)}, Lon=${destination.lon.toFixed(4)}`);
                
                if (destination.lat < -90) destination.lat = -90;
                if (destination.lat > 90) destination.lat = 90;
                if (destination.lon < -180) destination.lon = -180 + 360;
                if (destination.lon > 180) destination.lon = 180 - 360;
            }
            
            const arrivalTime = new Date(lastPoint.time + (projectionTime * 60 * 1000));
            const arrivalTimeStr = arrivalTime.toLocaleTimeString([], {hour: '2-digit', minute: '2-digit'});
            
            projectedPosition = {
                lat: destination.lat,
                lon: destination.lon,
                distance: projectedDistanceKm,
                estimatedArrival: arrivalTimeStr,
                useAcceleration,
                finalVelocity: finalVelocityMps
            };
            
            clearProjection();
            
            const destMarker = L.marker([destination.lat, destination.lon], {
                icon: L.divIcon({
                    html: useAcceleration ? 
                        '<div style="background: #ff00ff; color: #fff; width: 28px; height: 28px; border-radius: 50%; border: 3px solid #fff; display: flex; align-items: center; justify-content: center; font-weight: bold; font-size: 12px; box-shadow: 0 0 10px #ff00ff;"></div>' :
                        '<div style="background: #00ff00; color: #000; width: 28px; height: 28px; border-radius: 50%; border: 3px solid #000; display: flex; align-items: center; justify-content: center; font-weight: bold; font-size: 12px; box-shadow: 0 0 10px #00ff00;"></div>',
                    iconSize: [28, 28],
                    className: 'destination-marker'
                }),
                title: `Destino em ${projectionTime} minutos`
            }).addTo(map);
            
            destMarker.bindTooltip(
                `Destino em ${projectionTime} min<br>` +
                `Distncia: ${projectedDistanceKm.toFixed(1)} km<br>` +
                `Velocidade final: ${finalVelocityMps.toFixed(1)} m/s (${(finalVelocityMps * 3.6).toFixed(1)} km/h)<br>` +
                `Chegada: ${arrivalTimeStr}<br>` +
                `Mtodo: ${useAcceleration ? 'MRUV (com acelerao)' : 'MRU (sem acelerao)'}`,
                { permanent: false, direction: 'top' }
            );
            
            leafletMarkers.push(destMarker);
            
            projectionLine = L.polyline([
                [lastPoint.lat, lastPoint.lon],
                [destination.lat, destination.lon]
            ], {
                color: useAcceleration ? '#ff00ff' : '#00ff00',
                weight: 3,
                opacity: 0.8,
                dashArray: useAcceleration ? '10, 10' : null,
                lineCap: 'round'
            }).addTo(map);
            
            console.log('\n RESULTADO DA PROJEO:');
            console.log('='.repeat(60));
            console.log(`   Posio: ${destination.lat.toFixed(4)}, ${destination.lon.toFixed(4)}`);
            console.log(`   Distncia: ${projectedDistanceKm.toFixed(1)} km`);
            console.log(`   Velocidade final: ${finalVelocityMps.toFixed(1)} m/s (${(finalVelocityMps * 3.6).toFixed(1)} km/h)`);
            console.log(`   Chegada: ${arrivalTimeStr}`);
            console.log(`   Mtodo: ${useAcceleration ? 'MRUV (Movimento Uniformemente Variado)' : 'MRU (Movimento Uniforme)'}`);
            console.log('='.repeat(60));
            
        } catch (error) {
            console.error(' ERRO AO PROJETAR TRAJETRIA:', error);
            alert('Erro ao projetar trajetria: ' + (error as Error).message);
        }
    }

    function clearProjection() {
        if (projectionLine && map && map.hasLayer(projectionLine)) {
            map.removeLayer(projectionLine);
            projectionLine = null;
        }
        
        const mapObj = getMap();
        if (mapObj) {
            const markersToRemove: number[] = [];
            
            leafletMarkers.forEach((marker, index) => {
                if (marker.options.icon?.options?.className === 'destination-marker') {
                    if (mapObj.hasLayer(marker)) {
                        mapObj.removeLayer(marker);
                        markersToRemove.push(index);
                    }
                }
            });
            
            markersToRemove.reverse().forEach(index => {
                leafletMarkers.splice(index, 1);
            });
        }
        
        projectedPosition = null;
        console.log(' Projeo limpa');
    }

    function clearAll() {
        console.log(' INICIANDO LIMPEZA TOTAL...');
        
        clearProjection();
        
        points = [];
        lastCalculation = null;
        projectedPosition = null;
        
        const mapObj = getMap();
        if (mapObj) {
            leafletMarkers.forEach(marker => {
                if (mapObj.hasLayer(marker)) {
                    mapObj.removeLayer(marker);
                }
            });
            
            leafletLines.forEach(line => {
                if (mapObj.hasLayer(line)) {
                    mapObj.removeLayer(line);
                }
            });
        }
        
        leafletMarkers = [];
        leafletLines = [];
        
        trackingMode = false;
        
        console.log(' TODOS OS DADOS FORAM LIMPOS COMPLETAMENTE');
    }

    function getMap(): any {
        return map;
    }

    onMount(() => {
        console.log(' Plugin Convective Tracker iniciando...');
        console.log(' Nome do plugin:', name);
        console.log(' CONFIGURAO DO SISTEMA:');
        console.log('   - Sensibilidade da acelerao:', SIGNIFICANT_ACCELERATION, 'm/s');
        console.log('   - Detecta acelerao por valor absoluto OU mudana percentual > 10%');
        console.log('   - MRU padro: DESATIVADO (teste MRUV primeiro)');
        console.log('   - Boto "Limpar Projeo": SEMPRE ATIVO');
        
        setCurrentTime();
        
        if (singleclick && typeof singleclick.on === 'function') {
            try {
                console.log(' Configurando singleclick listener...');
                
                singleclick.on(name, (latLon: any) => {
                    console.log(' Clique recebido:', latLon);
                    handleMapClick(latLon.lat, latLon.lon);
                });
                
                console.log(' Listener configurado!');
                
            } catch (error) {
                console.error(' Erro ao configurar singleclick:', error);
            }
        }
        
        console.log('\n INSTRUES PARA TESTE:');
        console.log('   1. Desmarque "Usar movimento uniforme" para testar MRUV');
        console.log('   2. Marque 3+ pontos com intervalos regulares');
        console.log('   3. Veja no console se acelerao  detectada como SIGNIFICANTE');
        console.log('   4. Projete trajetria e compare MRU vs MRUV');
        console.log('   5. Use "Limpar Projeo" a qualquer momento');
        console.log('   6. Use "Limpar Tudo" para reiniciar completamente');
        console.log('\n Plugin Convective Tracker carregado e pronto!');
    });

    onDestroy(() => {
        console.log(' Plugin sendo destrudo...');
        
        if (singleclick && typeof singleclick.off === 'function') {
            try {
                singleclick.off(name);
                console.log(' Listener singleclick removido');
            } catch (error) {
                console.error(' Erro ao remover listener:', error);
            }
        }
        
        clearAll();
    });
</script>

