    // Verso no formato semver (ex: 1.0.0)
    version: '1.0.0',
    
    // cone emoji (pode ser qualquer emoji)
    icon: '',
    
    // Ttulo que aparece no menu
    title: 'Convective Formation Tracker',
    
    // Descrio na galeria de plugins
    description: 'Tracker para monitorar e prever deslocamento de formaes convectivas',
    
    // Seu nome ou organizao
    author: 'Andrew Lemos',
    
    // Layout no desktop: 'rhpane' (lado direito) ou 'embedded' (canto inferior)
    desktopUI: 'rhpane',
    
    // Layout no mobile: 'fullscreen' ou 'small'
    mobileUI: 'fullscreen',
    
    // Largura do painel direito (apenas para desktopUI: 'rhpane')
    desktopWidth: 450,
    
    // Aparece no menu de contexto (boto direito no mapa)
    addToContextmenu: true,
    
    // URL do plugin (permite acesso direto via link)
    routerPath: '/convective-tracker/:lat?/:lon?',
    
    // Ouve cliques no mapa quando o plugin est aberto
    listenToSingleclick: true,
